/* Write your CSS here */
* {
  margin: 0px;
  box-sizing: border-box;
}
.cont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.google-img {
  width: 200px;
  margin: 20px;
}
.card {
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: 0px 4px 16px 0px;

  border-radius: 20px;
}
.search-icon {
  width: 20px;
}
.search-item {
  display: flex;
  margin: 20px;
}
.ul-li {
  display: flex;
  flex-direction: column;
}
.input-ele {
  width: 400px;
  border: 1px solid white;
}
